.header{
    box-sizing:border-box;
    margin: 30px 0 0 10px;
}

.headerText{
    float: left;
    box-sizing: border-box;
    width: 55%;
    padding-bottom: 10px;

}

.headerVideo{
    box-sizing: border-box;
    float: right;
    width: 45%;
    padding: 0 0 30px 30px;
}

.headerVideo iframe{
    width: 100%;
    min-height: 200px;
}

.headerVideo a{
    box-sizing: border-box;
    display: block;
    text-align: center;
    width: 100%;
    background-color: rgb(183, 227, 241);
    cursor: pointer;
    margin-top: 10px;
    padding: 5px;
    border-radius: 20px;
    text-decoration: none;
    color: black;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    min-height: 32px;
}
.headerVideo a:hover{
    background-color: rgb(192, 239, 255);
    box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.5);
}

.longText{
    margin: 0 0 0 3px;
    font-size: 18px;
    font-weight: 300;
}

.keyText{
    margin: 0;
    font-size: 35px;
    color: #3e3e3e;
}

.content{
    margin-left: 10px;
    color: #525252;
}

@media only screen and (min-width: 2560px){
    .headerVideo{
        width: 25%;
    }

    .headerText{
        width: 75%;
    }

}

@media only screen and (min-width: 2000px) and (max-width: 2559px){
    .headerVideo{
        width: 30%;
    }

    .headerText{
        width: 70%;
    }

}

@media only screen and (min-width: 1600px) and (max-width: 1599px){
    .headerVideo{
        width: 36%;
    }

    .headerText{
        width: 64%;
    }
}

@media only screen and (max-width: 750px) {
    .headerText{
        float: none;
        width: 100%;
        padding-bottom: 10px;
    
    }
    .headerVideo{
        float: none;
        width: 380px;
        padding: 0;
    }
    
    .headerVideo a{
        margin-left: 0;
    }

    .headerVideo a{
        width: 80%;
        margin: 10px auto
    } 
}

@media only screen and (max-width: 425px) {
    .longText{
        font-size: 18px;
    }
    
    .keyText{
        font-size: 30px;
    }
    .headerVideo{
        width: 320px;
    }
}

@media only screen and (max-width: 370px) {
    .longText{
        font-size: 18px;
    }
    
    .keyText{
        font-size: 25px;
    }
    .headerVideo{
        width: 300px;
    }
}

@media only screen and (max-width: 340px) {
    .headerVideo{
        width: 250px;
    }
}